.app {
  text-align: center;
  min-height: 100vh;
  background: rgb(107, 185, 211);
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.tweet {
  border: 2px solid black;
  height: 300px;
  width: 300px;
  background: white;
  border-radius: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.red{
  color: red;
  font-size: 50px;
}
nav {
  background-color: black;
  color: white;
  padding-left: 2vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 10vh;
  width: 100vw;
}
.nav-links{
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
  float: right;
}
.nav-links-list{
  padding-right: 2vw;
}
a {
  color: white;
  text-decoration: none;
}

ol, ul {
  padding-left: 30px;
}

.board {
  align-items: center;
  text-align: center;
  position: absolute;
  left: 50%;
  top: 50%;
  transform:translate(-50%, -50%);
}
.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.game-info {
  text-align: center;
  font-size: 50px;
  justify-content: space-around;
}

.game-info-button{
  background-color: black;
  border: none;
  color: white;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  padding: 15px 32px;
  margin-right: 20px;
  margin-top: 20px;
}
.square {
  background: #fff;
  border: 1px solid black;
  float: left;
  font-size: 50px;
  font-weight: bold;
  line-height: 34px;
  min-height: 50px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 50px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
